.component-tooltip{position:absolute;-webkit-transition:-webkit-transform .3s ease;transition:-webkit-transform .3s ease;transition:transform .3s ease;transition:transform .3s ease, -webkit-transform .3s ease}.component-tooltip .tooltip-container{font-size:10px;line-height:22px;overflow:hidden;height:22px;padding:0 8px;opacity:.9;color:#fff;border-radius:2px;background:#747474}.component-tooltip.tooltip-enter,.component-tooltip.tooltip-leave{-webkit-transform:scale(1.1);transform:scale(1.1);opacity:0}



