.component-button{overflow:hidden}.component-button .ripple{position:absolute;display:block;width:4px;height:4px;-webkit-transform:scale(100);transform:scale(100);-webkit-animation:button-ripple 1.4s;animation:button-ripple 1.4s;opacity:0;border-radius:50%;background:#fff}@-webkit-keyframes button-ripple{from{-webkit-transform:scale(0);transform:scale(0);opacity:.5}to{-webkit-transform:scale(100);transform:scale(100);opacity:0}}@keyframes button-ripple{from{-webkit-transform:scale(0);transform:scale(0);opacity:.5}to{-webkit-transform:scale(100);transform:scale(100);opacity:0}}



