.component-snackbar{position:fixed;bottom:0;left:50%;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;min-width:288px;max-width:568px;padding:14px 24px;-webkit-transform:translate(-50%);transform:translate(-50%);color:#fff;border-radius:2px;background-color:#323232;-webkit-box-pack:justify;-webkit-justify-content:space-between;-ms-flex-pack:justify;justify-content:space-between}.component-snackbar .action{margin-left:48px;cursor:pointer}.component-snackbar.snackbar-transition{-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out}.component-snackbar.snackbar-enter,.component-snackbar.snackbar-leave{bottom:-50px}



