.date-picker .date-header{height:72px;padding:20px;text-align:left;background:#0884ce}.date-picker .date-header h4,.date-picker .date-header h2{color:rgba(255,255,255,0.7);cursor:pointer}.date-picker .date-header h4{font-weight:400;margin:0}.date-picker .date-header .date-year{overflow:hidden;height:16px}.date-picker .date-header .date{overflow:hidden;height:40px;margin:15px 0 0 0}.date-picker .date-header .date h2{font-size:36px;margin:0}.date-picker .date-header .ani-up{-webkit-animation:runUp .4s;animation:runUp .4s}.date-picker .date-header .ani-down{-webkit-animation:runDown .4s;animation:runDown .4s}.date-picker .date-header .active{color:#fff}@-webkit-keyframes runUp{0%{-webkit-transform:translateY(-20px)}100%{-webkit-transform:translateY(0)}}@-webkit-keyframes runDown{0%{-webkit-transform:translateY(20px)}100%{-webkit-transform:translateY(0)}}



