.date-picker{position:fixed;z-index:998;top:0;left:0;width:100%;height:100%;-webkit-transition:opacity .3s ease;transition:opacity .3s ease;background-color:rgba(0,0,0,0.5)}.date-picker .date-picker-wrapper{position:absolute;right:0;left:0}.date-picker .date-picker-wrapper .date-picker-container{z-index:999;width:320px;margin:0 auto;-webkit-user-select:none;-webkit-transition:all .3s ease;transition:all .3s ease;text-align:center;border-radius:2px;background:#fff;box-shadow:0 14px 45px rgba(0,0,0,0.25),0 10px 18px rgba(0,0,0,0.22)}.date-picker.date-enter,.date-picker.date-leave{opacity:0}.date-picker.date-enter .date-picker-container,.date-picker.date-leave .date-picker-container{-webkit-transform:scale(1.1);transform:scale(1.1)}



