.component-dialog{position:fixed;z-index:9998;top:0;left:0;display:table;width:100%;height:100%;-webkit-transition:opacity .3s ease;transition:opacity .3s ease;background-color:rgba(0,0,0,0.15)}.component-dialog .dialog-wrap{display:table-cell;vertical-align:middle}.component-dialog .dialog-wrap .dialog-container{position:relative;padding:14px;margin:0 auto;-webkit-transition:all .3s ease;transition:all .3s ease;border-radius:2px;background-color:#fff;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;-ms-grid-row-align:center;align-items:center;box-shadow:0 9px 46px 8px rgba(0,0,0,0.14),0 11px 15px -7px rgba(0,0,0,0.12),0 24px 38px 3px rgba(0,0,0,0.2)}.component-dialog .dialog-wrap .dialog-container .dialog-content{padding:20px 24px 24px 24px;color:rgba(0,0,0,0.54)}.component-dialog .dialog-wrap .dialog-container .dialog-actions{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-webkit-flex-direction:row-reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse;-webkit-flex-wrap:wrap;-ms-flex-wrap:wrap;flex-wrap:wrap}.component-dialog.dialog-enter,.component-dialog.dialog-leave{opacity:0}.component-dialog.dialog-enter .dialog-container,.component-dialog.dialog-leave .dialog-container{-webkit-transform:scale(1.1);transform:scale(1.1)}



