.component-avatar{display:inline-block}.component-avatar.large .avatar{line-height:60px;width:60px;height:60px}.component-avatar.large .avatar .letter{font-size:26px}.component-avatar.small .avatar{line-height:24px;width:24px;height:24px}.component-avatar.small .avatar .letter{font-size:12px}.component-avatar .avatar{font-size:0;line-height:41px;display:inline-block;box-sizing:border-box;width:41px;height:41px;text-align:center;vertical-align:middle;color:#fff;border-radius:50%}.component-avatar .avatar.is-letter{background-color:#0884ce}.component-avatar .avatar .img{display:block;width:100%;height:100%;border-radius:50%;background-repeat:no-repeat;background-position:center}.component-avatar .avatar .letter{font-size:18px}



