.component-select{position:relative;display:inline-block}.component-select .select-input{font-size:16px;line-height:32px;position:relative;display:inline-block;box-sizing:border-box;width:100%;min-width:180px;height:32px;padding-left:10px;border-bottom:1px solid #e0e0e0;outline:none}.component-select .select-input:focus,.component-select .select-input:hover{border-bottom:2px solid #3c80f6}.component-select .select-input:focus .select-triangle svg,.component-select .select-input:hover .select-triangle svg{color:#3c80f6}.component-select .select-input .select-triangle{position:absolute;right:5px;width:12px;-webkit-transform:rotate(90deg);transform:rotate(90deg);color:rgba(0,0,0,0.54)}.component-select .select-input .select-triangle svg{-webkit-transition:-webkit-transform .3s ease-in-out;transition:-webkit-transform .3s ease-in-out;transition:transform .3s ease-in-out;transition:transform .3s ease-in-out, -webkit-transform .3s ease-in-out}.component-select .select-input .select-triangle.open svg{-webkit-transform:rotate(180deg);transform:rotate(180deg)}.component-select .list-box{position:absolute;z-index:1;top:32px;overflow:hidden;overflow-y:auto;width:180px;max-height:300px;-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out;border-radius:2px;background-color:#fff}.component-select .list-box .select-search-bar{font-size:12px;height:10px;border:none;border-bottom:1px solid rgba(0,0,0,0.16);border-radius:0}.component-select .list-box .select-search-bar .search-icon{width:12px;height:12px}.component-select .list-box li{overflow:hidden;padding:5px 0;cursor:pointer;white-space:nowrap;text-indent:20px;text-overflow:ellipsis;color:rgba(0,0,0,0.87)}.component-select .list-box li:focus,.component-select .list-box li:hover,.component-select .list-box li.active{background:rgba(0,0,0,0.031)}.component-select .list-box.select-list-transition{-webkit-transition:all .3s ease-in-out;transition:all .3s ease-in-out}.component-select .list-box.select-list-enter,.component-select .list-box.select-list-leave{height:0;opacity:0}



