.component-checkbox{cursor:pointer}.component-checkbox.disabled .checkbox-tag{display:none}.component-checkbox.disabled .checkbox-bd{border:2px solid #c1c1c1 !important;background-color:#c1c1c1}.component-checkbox .ripple{position:absolute;top:50%;left:50%;display:block;width:4px;height:4px;margin:-2px;-webkit-transform:scale(13);transform:scale(13);-webkit-animation:checkbox-ripple .8s;animation:checkbox-ripple .8s;opacity:0;border-radius:50%;background:rgba(0,0,0,0.5)}@-webkit-keyframes checkbox-ripple{from{-webkit-transform:scale(0);transform:scale(0);opacity:.5}to{-webkit-transform:scale(13);transform:scale(13);opacity:0}}@keyframes checkbox-ripple{from{-webkit-transform:scale(0);transform:scale(0);opacity:.5}to{-webkit-transform:scale(13);transform:scale(13);opacity:0}}



