.component-tabs{position:relative;height:48px;background:#3993e5}.component-tabs .tabs{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;width:100%;height:100%;text-align:center;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center}.component-tabs .tabs .tabs-item{cursor:pointer;color:rgba(255,255,255,0.7);-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1}.component-tabs .tabs .tabs-item.active{color:#fff}.component-tabs .indicator{position:absolute;bottom:0;left:0;height:2px;-webkit-transition:all .3s ease-out;transition:all .3s ease-out;background:#ffff86}



