.date-picker .date-footer{padding:15px 10px 10px 10px;text-align:right}.date-picker .date-footer button{padding:8px 20px;cursor:pointer;color:red;color:#0884ce;border:none;background:transparent}.date-picker .date-footer button:hover{-webkit-animation:btn-fade-in .5s ease both;animation:btn-fade-in .5s ease both;color:#fff}.date-picker .date-footer button:focus{outline:none}@-webkit-keyframes btn-fade-in{0%{background:rgba(9,148,231,0.1)}100%{background:rgba(9,148,231,0.5)}}



