.component-table{position:relative;width:100%;margin:0 auto;padding-top:48px;border:1px solid rgba(0,0,0,0.12);background:#fff}.component-table>.container{max-height:300px;overflow-y:auto}.component-table>.container table{width:100%;border-spacing:0}.component-table>.container table td,.component-table>.container table th{font-size:14px;font-weight:100;line-height:48px;height:48px;padding:0 25px;color:#757575}.component-table>.container table th{line-height:0;height:0;padding-top:0;padding-bottom:0;white-space:nowrap;color:transparent;border:none}.component-table>.container table thead th>div{line-height:normal;position:absolute;top:0;margin-left:-25px;padding:15px 25px;color:rgba(0,0,0,0.54);background:transparent}.component-table>.container table tbody tr{cursor:pointer}.component-table>.container table tbody tr td{border-top:1px solid rgba(0,0,0,0.12)}.component-table>.container table tbody tr.active{background:#f5f5f5}.component-table>.container table tbody tr:hover{background:#eee}



